.header {
    border-bottom: 1px solid #545454;
    padding: 45px 0 41px;
    max-width: 880px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 896px) {
    .header {
        max-width: 581px;
        padding: 28px 0 31.6px 27px;
    }
}